@import "core/colors";

.sweeter {
  position: relative;
}
.loader {
  width: 100vw;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: $clr-red;
  z-index: 999;
  transition: opacity ease 300ms;

  &.loaded {
    opacity: 0;
    pointer-events: none;
  }
}
